import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { UserService } from '../../@core/services';
import { ToastrComponent } from '../../@core/components';
import  {Router,ActivatedRoute} from '@angular/router'

@Component({
  selector: 'ngx-conform-merchant',
  templateUrl: './conform-merchant.component.html',
  styleUrls: ['./conform-merchant.component.scss']
})
export class ConformMerchantComponent implements OnInit {
  is_pw_change = false
  allParams
  is_alert = false
  is_loading = false
  message = ''
  userMessage
  fieldTextType: boolean;
  fieldTextTypeRepeat: boolean;
  is_fixed_btn_disable: boolean = false;

  login_form : FormGroup

  constructor(
    fb: FormBuilder, private user_service: UserService, private route: ActivatedRoute, private router: Router, private toastrComponent: ToastrComponent,
  ) { 
    this.login_form = fb.group({
      "new_password": ['', [Validators.required, Validators.minLength(8),Validators.pattern('^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&#])[A-Za-z\d$@$!%*?&#].{7,}$')]],
      "confirm_password": ['', [Validators.required, Validators.minLength(8),Validators.pattern('^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&#])[A-Za-z\d$@$!%*?&#].{7,}$')]],

    });
  }

  ngOnInit(): void {
    this.allParams = this.route.snapshot.queryParams
  }
  verify_merchant() {
    this.is_loading = true

    let password = this.login_form.value['new_password']
    let psw_repeat = this.login_form.value['confirm_password']

    if (!password || !psw_repeat) {
      // this.showAlert('Required Password')
      this.toastrComponent.showToast("danger", "Oh Snap!", "Required Password");
      this.is_loading = false
      return;
    }

    if (password != psw_repeat) {
      // this.showAlert('The passwords did not match. Please try again')
      this.toastrComponent.showToast("danger", "Oh Snap!", "The passwords did not match. Please try again");
      this.is_loading = false
      console.log('Password Missmatched')
      return;
    }
    console.log('xx33')
    let data = {
      user_id: this.allParams['user_id'],
      token: this.allParams['token'],
      new_password: password,
      confirm_password: psw_repeat
    }

    this.user_service.verifyMerchantData(data).then(res=>{
    
      if (res['status'] == 100) {
        this.is_loading = false
        this.toastrComponent.showToast("success", "Cheers!", "Success.");
        this.router.navigate(['/authentication/login'])
        
      } else {
        this.userMessage = res['message']
        this.is_loading = false
        // this.showAlert(this.userMessage)
        this.toastrComponent.showToast("danger", "Oh Snap!", this.userMessage);
       
       
      }
    })
  }
  showAlert(message) {
    this.is_loading = false
    this.is_alert = true
    this.message = message
    setTimeout(() => {    
      this.is_alert = false
    }, 2000);
  }
  // showAlertSucces(message) {
  //   this.is_alert_succes = true
  //   this.message = message
  //   setTimeout(() => {    
  //     this.is_alert_succes = false
  //   }, 2000);
  // }
  // password validators
  toggleFieldTextType() {
    this.fieldTextType = !this.fieldTextType;
  }
  toggleFieldTextTypeRepeat() {
    this.fieldTextTypeRepeat = !this.fieldTextTypeRepeat;
  }
  button_disability(): boolean {
    if (this.is_fixed_btn_disable) {
      return true
    } else {
      return !this.login_form.valid
    }
  }


}
