import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AuthenticationComponent } from './authentication.component';
import { LoginComponent } from './login/login.component';
import { ForgetPasswordComponent } from './forget-password/forget-password.component';
import { OtpVerrificationComponent} from './otp-verrification/otp-verrification.component';
import { ConformPwComponent } from './conform-pw/conform-pw.component';
import { ConformMerchantComponent} from './conform-merchant/conform-merchant.component';
import { UserManagementResetPwComponent} from './user-management-reset-pw/user-management-reset-pw.component'

const routes: Routes = [
  {
    path: '',
    component: AuthenticationComponent,
    children: [
      {
        path: 'login',
        component: LoginComponent,
      },
      {
        path: 'forget-password',
        component: ForgetPasswordComponent,
      },
      {
        path: 'otp-verrification',
        component: OtpVerrificationComponent,
      },
      {
        path: 'reset-password',
        component: ConformPwComponent,
      },
      {
        path: 'password-reset',
        component: ConformMerchantComponent,
      },
      {
        path: 'um-password-reset/:id',
        component: UserManagementResetPwComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AuthenticationRoutingModule {
}
